$blue: #0277BD;

.App {
  font-family: sans-serif;
  text-align: center;
  direction: rtl;
  font-size: 16px;
}

table {
  width: 100%;
  border: 1px solid black;
  padding: 0;
  margin: 0;
  border-spacing: 0;
  border-collapse: separate;
}

table thead th {
  border-bottom: 3px solid black;
  padding: 10px 0;
}

table tr td {
  border-bottom: 1px solid black;
  padding: 5px 0;
  margin: 0;
}

table tr:nth-child(8) td {
  border-color: #900c3f;
}

table tr:last-child td {
  border: none;
}

table tr:nth-child(n+0):nth-child(-n+2){
  color :green;
}

table tr:nth-child(n+0):nth-child(n+9){
  color :red;
}

.bold {
  font-weight: bold;
  background: #42adf5;
  color: white;
}


.single-fixture {
  border: 2px solid $blue
  ;
  margin: 0px 0;
  padding: 0;
  display: flex;
  width: 100%;

  span {
    display: inline-block;
    padding: 0;
    box-sizing: border-box;
    line-height: 30px;  
    width: 40%;
    
    &.draw {
      width: 20%;
      border-right: 3px solid $blue
      ;
      border-left: 3px solid $blue
      ;
    }
  }
}

.third-round {
  display: flex;
  flex-direction: row;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-around;
  .single-round {
    width: 24%;
    min-width: 230px;
    margin: 10px;
  }

  // .bold {
  //   background: white;    
  //   font-weight: initial;
  // }


}

.team{
  &-hpt{color: #036bfc;}
  &-ako, &-afu, &-bei, &-rms{color: blue;}
  &-rei, &-ksm, &-ash, &-rmg, &-uef, &-ran{color: red;}
  &-kfs, &-naz{color: green;}
  &-ris, &-nsz, &-bni { color: orange;}
}

.topSection {
  display: flex;
  justify-content: space-around;
}

.width-50{
  width: 48%;
}